import React, { useState, useEffect } from 'react';
import { VictoryChart, VictoryScatter, VictoryAxis, VictoryTheme ,VictoryLine, VictoryLabel} from 'victory';
import styled from 'styled-components';

const colors = ["#A8E6CE", "#DCEDC2", "#FFD3B5", "#FFAAA6", "#FF8C94"];

const ScatterPoint = ({ x, y, datum, min, max }) => {
    const i = React.useMemo(() => {
        return Math.floor(((datum.y - min) / (max - min)) * (colors.length - 1));
    }, [datum, min, max]);

    return <circle fill={colors[i]} cx={x} cy={y} r={6} />;
};
const parseTime = (timeString) => {
    const [hours, minutes] = timeString.split(':').map(parseFloat);
    return hours + minutes / 60;
};


const AVGTimes = {
    '': 2, // 02:00
    '': 3.3, // 03:30
    '': 4.1, // 04:10
    '': 4, // 04:00
    '': 3.2, // 03:20
    '': 3.5, // 03:50
    '': 2.5, // 02:50
    '': 1.3, // 02:50
};


const TrafficChart = () => {
    const [data, setData] = useState(null);

    useEffect(() => {
        const fetchData = async () => {
            try {
                const response = await fetch('https://data.ex.co.kr/openapi/safeDriving/forecast?key=9710729048&type=json');
                const result = await response.json();
                //  'list'     .
                setData(result.list[0]);
            } catch (error) {
                console.error('Error fetching data:', error);
            }
        };

        fetchData();
    }, []);

    if (!data) {
        return <div>Loading...</div>;
    }

    //      .
    const avgTimes = {
        '': (parseTime(data.csudj) + parseTime(data.csudj_bus)) / 2,
        '': (parseTime(data.csudg) + parseTime(data.csudg_bus)) / 2,
        '': (parseTime(data.csuus) + parseTime(data.csuus_bus)) / 2,
        '': (parseTime(data.csubs) + parseTime(data.csubs_bus)) / 2,
        '': (parseTime(data.csugj) + parseTime(data.csugj_bus)) / 2,
        '': (parseTime(data.csump) + parseTime(data.csump_bus)) / 2,
        '': (parseTime(data.csukr) + parseTime(data.csukr_bus)) / 2,
        '': (parseTime(data.csuyy) + parseTime(data.csuyy_bus)) / 2,
    };

    console.log(avgTimes)
    //    .
    const chartData = Object.keys(avgTimes).map(city => ({ x: city, y: avgTimes[city] }));


    // y    .
    const temperatures = chartData.map(({ y }) => y);
    const min = Math.min(...temperatures);
    const max = Math.max(...temperatures);

    return (
        <div>
            <h4>    </h4>
            <VictoryChart domainPadding={20} theme={VictoryTheme.material}

                          style={{
                              parent: { background: "#fff" , border: "1px solid #ccc" } //   
                          }}>
                <VictoryLine
                    data={Object.keys(AVGTimes).map(city => ({ x: city, y: AVGTimes[city] }))}
                    style={{
                        data: {stroke: "black"}
                    }}

                />
                <VictoryAxis
                    // X  
                    tickValues={Object.keys(AVGTimes)} //    
                    tickFormat={(city) => city} //   
                    label="" // X  
                    style={{
                        axisLabel: { padding: 30 } //     
                    }}

                />
                <VictoryAxis/>
                <VictoryAxis dependentAxis
                             tickValues={["0", "1", "2", "3", "4", "5", "6", "7"]}
                             label="  ()"
                             style={{
                             axisLabel: { padding: 30 } // Y     
                             }}
                />
                <VictoryScatter
                    data={chartData}
                    dataComponent={<ScatterPoint min={min} max={max}/>}
                />
                {/*<VictoryLine*/}
                {/*    data={Object.keys(AVGTimes).map(city => ({ x: city, y: AVGTimes[city] }))}*/}
                {/*    style={{*/}
                {/*        data: {stroke: "black"}*/}
                {/*    }}*/}

                {/*/>*/}
                <VictoryLabel
                    text=":   , :   "
                    x={170} //   
                    y={50} //   
                    textAnchor="middle" //    
                    style={{ fontSize: 10 }} //   
                />
            </VictoryChart>

        </div>
    );
};

export default TrafficChart;
